import getSize from '../../SettingsMenu/Resize/getSize';
import Redraw from '../ToolsFunction/Redraw';


export default function Dithering () {
    const canvasDrawing = document.querySelector('.canvas__drawing');
    const ctx = canvasDrawing.getContext('2d');
    const tool = document.querySelector('.tool__dithering');
    let color;


    const draw = (e) => {
        const canvasSize= getSize();
        if (tool.classList.contains('tool--active')) { 
            if (e.which === 1) {
                color = document.querySelector('.color__primary').value;
            }
            if (e.which === 3) {
                color = document.querySelector('.color__secondary').value;
            }
            let x0 = e.offsetX;
            let y0 = e.offsetY;
            const activeFrame = document.querySelector('.frame--active');
            const ctxFrame = activeFrame.getContext('2d');
            const widthPixel = canvasDrawing.width / canvasSize;
            let x1 = null;
            let y1 = null;
            let coordX = null;
            let coordY = null;
            let size = 1;
            canvasDrawing.onmousemove = (event) => {
                x1 = event.offsetX;
                y1 = event.offsetY;
                const dx = Math.abs(x1 - x0);
                const dy = Math.abs(y1 - y0);
                const sx = x0 < x1 ? 1 : -1;
                const sy = y0 < y1 ? 1 : -1;
                let err = dx - dy;
        
                while (true) {
                    coordX = Math.floor(x0 / widthPixel);
                    coordY = Math.floor(y0 / widthPixel);
                    ctx.fillStyle = color;
                    ctxFrame.fillStyle = color;
                    let startX = coordX * widthPixel - widthPixel;
                    let startY = coordY * widthPixel - widthPixel;
                    if (size === 1) {
                        startX = coordX * widthPixel;
                        startY = coordY * widthPixel;
                    }
                    startX = coordX * widthPixel;
                    startY = coordY * widthPixel;
                    const K = (32/canvasSize) *40;
                    if ((startX % K === 0 && startY % K === 0) || (startX % K !== 0 && startY % K !== 0)) {
                        ctx.fillStyle = document.querySelector('.color__primary').value;
                        ctx.fillRect(startX, startY, widthPixel, widthPixel);
                    } else {
                        ctx.fillStyle = document.querySelector('.color__secondary').value;
                        ctx.fillRect(startX, startY, widthPixel, widthPixel);
                    }
                    if (x0 === x1 && y0 === y1) break;
                    const err2 = 2 * err;
                    if (err2 > -dy) {
                        err -= dy;
                        x0 += sx;
                    }
                    if (err2 < dx) {
                        err += dx;
                        y0 += sy;
                    }
                    Redraw();
                }
            };
            document.addEventListener('mouseup', () => {
                canvasDrawing.onmousemove = null;
            });
        }
    }
    canvasDrawing.addEventListener('mousedown', draw, false);
}
