import React, { Component } from 'react';
import { GoogleLogin } from 'react-google-login';
import { GoogleLogout } from 'react-google-login';
import './autorize.css';


class Google extends Component {

    state = {
        isOpen: false,
        UserInfo: {},
        accessToken: 0
    }
    getUserInfo = (data) => {
        console.log('data', data);
        const UserInfo = {
            Name: data.profileObj.givenName,
            Surname: data.profileObj.familyName,
            imageUrl: data.profileObj.imageUrl
        }
        this.setState ( {
            UserInfo,
            accessToken: data.accessToken,
        })    
        console.log(this.state);
    }
    responseGoogle = (data) => {
        this.getUserInfo(data);
        this.setState ({
            isOpen: true,
        })
    }    
    logout = () => {
        this.setState ({
            isOpen: false,
        })
    }

    Upload = async () => {
        const fileContent = 'sample text'; // As a sample, upload a text file.
        const file = new Blob([fileContent], {type: 'text/plain'});
        const metadata = {
            'name': 'sampleName', // Filename at Google Drive
            'mimeType': 'text/plain', // mimeType at Google Drive
            'parents': [], // Folder ID at Google Drive
        };
        console.log('gegege');
        console.log(this.state.accessToken);
        const accessToken = this.state.accessToken;
        const form = new FormData();
        form.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
        form.append('file', file);
    
        fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart&fields=id', {
            method: 'POST',
            headers: new Headers({ 'Authorization': 'Bearer ' + accessToken }),
            body: form,
        }).then((res) => {
            console.log(res);
            return res.json();
        }).then(function(val) {
            console.log(val);
        });
    }
    onUpload = () => {
        // const downlPngBut = document.querySelector('.button__export--google');
        // downlPngBut.addEventListener('click', this.Upload, false);
    }
    componentDidMount() {
        this.onUpload();
    }
    render() {
        return (
            <>
            { !this.state.isOpen &&
                <GoogleLogin
                    clientId="844462840667-skmmkhrti2gs83j0t3q5gj15t2jvm1nu.apps.googleusercontent.com"
                    render={renderProps => (
                        <button className="button__header button__header--google" 
                            onClick={renderProps.onClick} 
                            disabled={renderProps.disabled}> 
                            Sign in                        
                        </button>
                    )}                    
                    onSuccess={this.responseGoogle}
                    // onFailure={responseGoogle}
                    cookiePolicy={'single_host_origin'}
                />
            }
            { this.state.isOpen && 
                <GoogleLogout
                    clientId="844462840667-skmmkhrti2gs83j0t3q5gj15t2jvm1nu.apps.googleusercontent.com"
                    buttonText="Logout"
                    render={renderProps => (
                        <button className="button__header button__header--google" 
                            disabled={renderProps.disabled}> 
                            <img src={this.state.UserInfo.imageUrl} alt="logo" className="user__logo"></img> 
                            <span className="user__info user__info--name">{this.state.UserInfo.Name}</span>
                            <span className="user__info user__info--surname">{this.state.UserInfo.Surname}</span>                            
                            <span className="button__logout" onClick={renderProps.onClick}></span>
                        </button>
                    )} 
                    onLogoutSuccess={this.logout}
                >
                </GoogleLogout>
            }
            </>
        );
    }
}

export default Google;
