import React from 'react';
import './App.css';
import Header from './components/header/Header';
import MainView from './View/MainView';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import Landing from './components/Landing/Landing';
import Footer from './components/Landing/Sections/Footer/Footer';

export default function App() {
  return (
    <Router>
        <Header/>
        <Route path="/piskelClone" component={Landing} exact/>
        <Route path="/piskelClone/newPiskel" component={MainView} exact/>
        <Route path="/piskelClone" component={Footer} exact/>
    </Router>
  );
}
 