import React, {Component} from 'react';
import './animatedPreview.css';

export default class AnimatedPreview extends Component {

    intervalID = 0;
    framePreview = 0;

    state = {
        fps: 12,
        currentFrame: 0,
        timerId: 0,
    }
    AnimatedFrames() {
        const canvasPreview = document.querySelector('.preview__canvas');
        const ctx = canvasPreview.getContext('2d');
        let currentFrame = 0;
        const draw = () => {
            if (this.state.fps > 0) {
                const frames = document.getElementsByClassName('frame');
                const framesCount = frames.length;
                ctx.clearRect(0, 0, 600, 600);
                if (frames[currentFrame]) {
                    ctx.drawImage(frames[currentFrame], 0, 0);
                }            
                if (currentFrame === framesCount-1) {
                    currentFrame = 0;
                } else {                    
                    currentFrame++;
                }
            }
        }
        return draw;        
    }
    onAnimatedFrames = () => {
        const draw =  this.AnimatedFrames();
        clearInterval(this.intervalID);
        this.intervalID = setInterval(draw, 1000/this.state.fps);
    }
    getFps = (e) => {
        const fps = e.target.value;
        return fps;
    }
    ChangeFps = (e) => {
        const fps = this.getFps(e);
        this.setState ({
            fps: fps,
        })
    }
    onChangeFps = () => {
        const fpsIpput = document.querySelector('.fps__range');
        fpsIpput.addEventListener('change', this.ChangeFps, false);
        fpsIpput.addEventListener('change',this.onAnimatedFrames, false);
    }
    offChangeFPs = () => {
        const fpsIpput = document.querySelector('.fps__range');
        fpsIpput.removeEventListener('change', this.ChangeFps, false);
    }
    FullScreen = () => {
        if(this.framePreview.requestFullscreen) {
            this.framePreview.requestFullscreen();
        } else if(this.framePreview.webkitrequestFullscreen) {
            this.framePreview.webkitRequestFullscreen();
        } else if(this.framePreview.mozRequestFullscreen) {
            this.framePreview.mozRequestFullScreen();
        }
      }
      onFullScreen() {
        this.framePreview = document.querySelector('.preview__canvas');
        const brnOnFullScreen = document.querySelector('.button__fullscreen')
        brnOnFullScreen.addEventListener('click', this.FullScreen, false);
      }
      offFullScreen() {
        const brnOnFullScreen = document.querySelector('.button__fullscreen')
        brnOnFullScreen.removeEventListener('click', this.FullScreen, false);  
      }
    componentDidMount() {
        this.onChangeFps();
        this.AnimatedFrames();
        this.onAnimatedFrames();
        this.onFullScreen();
    }
    componentWillUnmount() {
        this.offChangeFPs();
        clearInterval(this.intervalID);
        this.offFullScreen();
    }
    render () { 
        return (
            <div className="preview__container">
                <canvas className="preview__canvas" width="600" height="600"></canvas>
                <button className="button__fullscreen">Full Screen</button>
                <div className="fps__container">
                    <span className="fps__description">{this.state.fps}</span>
                    <input className="fps__range" min="0" max="24" type="range"></input>
                </div>
            </div>
    );
}}

