import getSize from '../../SettingsMenu/Resize/getSize';
import BresenhamsAlgoritm  from '../ToolsFunction/BresenhamsAlgoritm';


export default function Stroke() {

    let x1;
    let y1;
    let coordX1;
    let coordY1;
    const currentCanvas = document.querySelector('.canvas__drawing');
    const ctx = currentCanvas.getContext('2d');
    let pixelCount = getSize();
    let widthPixel = currentCanvas.width/pixelCount;
    let activeFrame;
    let ctxActiveFrame;
    let activeTool = document.querySelector('.tool--active');

  
    const drawLineMouseMove =  (e) => {
        const x2 = e.offsetX;
        const y2 = e.offsetY;
        ctx.clearRect(0, 0, currentCanvas.width, currentCanvas.height);
        ctx.drawImage(activeFrame, 0, 0);
        const coordX2 = Math.floor(x2 / widthPixel);
        const coordY2 = Math.floor(y2 / widthPixel);
        BresenhamsAlgoritm(coordX1, coordY1, coordX2, coordY2, widthPixel, ctx);
    }
    const drawLineMouseUp = (e) => {
        ctxActiveFrame.drawImage(currentCanvas, 0, 0);
        currentCanvas.removeEventListener('mousemove', drawLineMouseMove, false );
    }
    const drawLineMouseDown = (e) => {
        pixelCount = getSize();
        widthPixel = currentCanvas.width/pixelCount;
        let color;
        if (e.which === 1) {
            color = document.querySelector('.color__primary').value;
        }
        if (e.which === 3) {
            color = document.querySelector('.color__secondary').value;
        }
        activeFrame = document.querySelector('.frame--active');
        activeTool = document.querySelector('.tool--active');
        ctxActiveFrame = activeFrame.getContext('2d');
        if (activeTool.classList.contains('tool__stroke')) {
            x1 = e.offsetX;
            y1 = e.offsetY;
            coordX1 = Math.floor(x1/widthPixel);
            coordY1 = Math.floor(y1/widthPixel);
            ctx.fillStyle = color;
            currentCanvas.addEventListener('mousemove', drawLineMouseMove, false );
            currentCanvas.addEventListener('mouseup', drawLineMouseUp, false);
        }
        else {
            currentCanvas.removeEventListener('mousedown', drawLineMouseDown, false);
            currentCanvas.removeEventListener('mousemove', drawLineMouseMove, false);
            currentCanvas.removeEventListener('mouseup', drawLineMouseUp, false);
        }
    }
    currentCanvas.addEventListener('mousedown', drawLineMouseDown, false);
}
