export default function getColor() {
    const primaryInputColor = document.querySelector('.color__primary');
    const color = primaryInputColor.value.replace(/^#?([a-f\d])([a-f\d])([a-f\d])$/i
            ,(m, r, g, b) => '#' + r + r + g + g + b + b)
            .substring(1).match(/.{2}/g)
            .map(x => parseInt(x, 16));
    color.push(255);
    return color;
}
