import React, {Component} from 'react';
import './palette.css';


export default class Palette extends Component {    

    state = { 
        primaryColor: 0,
        secondaryColor: 0,
    }   
    changeColor = (e) => {
        if (e.target.classList.contains('color__primary')) {
            this.setState ({
                primaryColor: e.target.value,
            })
        }
        else {  
            this.setState({
                secondaryColor: e.target.value,
            })
        }
    }
    defaultValue() {
        const primaryColor = document.querySelector('.color__primary');
        primaryColor.value = '#000000';
        const secondaryColor = document.querySelector('.color__secondary');
        secondaryColor.value = '#ffffff';
    }
    onChangeColor() {
        const primaryColor = document.querySelector('.color__primary');
        const secondaryColor = document.querySelector('.color__secondary');
        primaryColor.addEventListener('change', this.changeColor, false);
        secondaryColor.addEventListener('change', this.changeColor, false);
    }
    componentDidMount() {
        this.onChangeColor();
        this.defaultValue();
    }
    render() {
        return (
            <div className="palette__wrapper">  
                <div className="color__wrapper color__wrapper--primary" style={{backgroundColor: this.state.primaryColor || '#000'}}>
                    <input className="color color__primary" type="color"></input>
                </div>       
                <div className="color__wrapper color__wrapper--secondary"  style={{backgroundColor: this.state.secondaryColor || '#fff'}}>
                    <input className="color color__secondary" type="color"></input>
                </div>              
            </div>            
        );
    }
}
