import React, {Component} from 'react';
import './pensize.css';

export default class Header extends Component {
    activeTool = 0;

    state = {
        size: 1,
    }
    
    changeSize = (e) => {
        if (!e.target.classList.contains('pensize__wrapper')) {          
            this.activeTool.classList.remove('pensize__option--active');
            e.target.classList.add('pensize__option--active');
            this.activeTool = e.target;
        }
        const activeSize = this.activeTool.dataset.size;
        this.setState ({
            size: activeSize,
        })
    }
    onChangeSize() {
        this.activeTool = document.querySelector('.pensize__option--active');
        const pensizeWrap = document.querySelector('.pensize__wrapper');
        pensizeWrap.addEventListener('click', this.changeSize, false);
    }
    getSize() {       
        return this.state.size;
    }
    componentDidMount() {
        this.onChangeSize();
        this.getSize();
    }
    render() {
        return (
            <div className="pensize__wrapper">
                <div className="pensize__option pensize__option--active" data-size="1"></div>
                <div className="pensize__option" data-size="2"></div>
                <div className="pensize__option" data-size="3"></div>
                <div className="pensize__option" data-size="4"></div>
            </div>
        );
    }
}

