import React, {Component} from 'react';
import './SettingMenu.css';
import Resize from './Resize/Resize';
import ExportGif from './Export/ExportGif';
import ExportPng from './Export/ExportPng';
export default class SettingMenu extends Component {

    PngExport = () => {
        const frames = document.querySelectorAll('.frame');
        const arrFrames = Array.from(frames);
        const canvas = document.createElement('canvas');
        canvas.width = 640;
        canvas.height = 640;
        const ctx = canvas.getContext('2d');
        arrFrames.forEach( (elem) => {
            ctx.drawImage(elem, 0, 0, 640, 640);
        });

        ExportPng(canvas, 'newPng');
    }
    onPngExport = () => {
        const downlPngBut = document.querySelector('.button__export--png');
        downlPngBut.addEventListener('click', this.PngExport, false);
    }
    onExport = () => {
        ExportGif();
    }
    componentDidMount() {
        this.onExport();
        this.onPngExport();
    }

    render() {
        return (
            <div className="settings__container">
                <div className="setting__hidden">
                    <Resize/>
                    <button className="button button__header button__export">Export as gif</button>
                    <button className="button button__header button__export--png">Export as png</button>
                    <button className="button button__header button__export--google">Google drive</button>
                    {/* <a className="link__save" href="1" download></a>                     */}
                    <button className="button__keyboard"></button>
                </div>       
            </div>
        );
    }      
}
