export default function Scale(e) {
    const canvasDrawing = document.querySelector('.canvas__drawing');
    let scale = +canvasDrawing.dataset.scale;
    const delta = e.deltaY || e.detail || e.wheelDelta;

    if (scale < 0.2 ) {
        scale = 0.21;
    }
    else if (delta > 0) {
        scale += 0.04;
    } else {
        scale -= 0.04;    
    }
    
    canvasDrawing.dataset.scale = scale;
    canvasDrawing.style.transform = `scale(${scale})`;
    e.preventDefault();
}
