import React, {Component} from 'react';
import Rotate from './tools/Rotate';
import Flip from './tools/Flip';
import './transformTools.css';
import Scale from './tools/Scale';

export default class TransformTools extends Component {
    
    onRotate() {
        const toolRotate = document.querySelector('.tool__rotate');
        toolRotate.addEventListener('click', Rotate, false);
    }
    onFlip() {
        const toolFlip = document.querySelector('.tool__flip');
        toolFlip.addEventListener('click', Flip, false);
    }
    onScale() {
        const canvasDrawing = document.querySelector('.canvas__drawing');
        canvasDrawing.addEventListener('wheel', Scale, {passive: false});
    }
    unmountTools() {
        const toolRotate = document.querySelector('.tool__rotate');
        toolRotate.removeEventListener('click', Rotate, false);
        const toolFlip = document.querySelector('.tool__flip');
        toolFlip.removeEventListener('click', Flip, false);
        const canvasDrawing = document.querySelector('.canvas__drawing');
        canvasDrawing.removeEventListener('wheel', Scale, false);
    }
    componentDidMount() {
        this.onRotate();
        this.onFlip();
        this.onScale();
    }
    render() {   
        return (
            <div className="transfrom__container">
                <h3 className="transform__title">Transform</h3>
                <ul className="tools__wrapper tools__wrapper--transform">
                    <li className="tool tool--fullsize tool__flip"></li>
                    <li className="tool tool--fullsize tool__rotate"></li>
                    <li className="tool tool__clone hidden"></li>
                    <li className="tool tool__center hidden"></li>
                </ul>
            </div>
        );
    }
        
}
