import Redraw from '../ToolsFunction/Redraw';

export default function Move(e) {
    const canvasDrawing = document.querySelector('.canvas__drawing');
    const ctx = canvasDrawing.getContext('2d');
    const tool = document.querySelector('.tool__move');

    const mouseUp = (e) => {
        canvasDrawing.onmousemove = null;
    }

    const draw = (e) => {
        if (tool.classList.contains('tool--active')) {
            const x1 = e.offsetX;
            const y1 = e.offsetY;
            const newCanvas = document.createElement('canvas');
            const context = newCanvas.getContext('2d');
            newCanvas.width = canvasDrawing.width;
            newCanvas.height = canvasDrawing.height;
            context.drawImage(canvasDrawing, 0, 0);
            canvasDrawing.onmousemove = (event) => {
                const x2 = event.offsetX;
                const y2 = event.offsetY;
                ctx.clearRect(0, 0, canvasDrawing.width, canvasDrawing.height);
                ctx.drawImage(newCanvas, x2 - x1, y2 - y1);
                };
                canvasDrawing.addEventListener('mouseup', () => {
                    canvasDrawing.onmousemove = null;
            });
            Redraw();
        } else {
            canvasDrawing.removeEventListener('mousedown', draw, false);
            canvasDrawing.removeEventListener('mouseup', mouseUp, false);
        }
    }
    canvasDrawing.addEventListener('mousedown', draw, false);
}
