import React, {Component} from 'react';
import './layerList.css';
import Layer from './Layer/Layer'

export default class LayerList extends Component {
    state = {
        layers: [
            {
                width: "640",
                height: "640",
                scale: "0.8",
                name: "Layer1",
                opacity: "0.4",
                zIndex: 1,
                id: 1
            },
        ],
        currentID: 2, 
        error: false, 
        activeLayer: 0,
    }

    counter = 0;

    onMount= () => {
        this.canvasDrawing = document.querySelector('.canvas__drawing');
        this.ctxDrawing = this.canvasDrawing.getContext('2d');
    }    

    changeActiveLayer = (e) => {
        const activeItem = document.querySelector('.item--active');
        const id = e.target.dataset.id;
        const allLayers = document.querySelectorAll('.canvas--layer');
        const arrLayers = Array.from(allLayers);
        const activeLayer = document.querySelector('.layer--active');
        const ctxActiveLayer = activeLayer.getContext('2d');
        arrLayers.forEach ( (item) => {
            if (+item.dataset.id === (this.state.currentID - 1) && this.counter === 0) {
                const ctx = item.getContext('2d');                
                ctx.drawImage(this.canvasDrawing, 0, 0);
                this.counter++;
            }
        })
        ctxActiveLayer.drawImage(this.canvasDrawing, 0, 0);
        arrLayers.forEach( (item) => {
            if (item.dataset.id === id) {
                this.ctxDrawing.clearRect(0, 0, this.canvasDrawing.width, this.canvasDrawing.height);
                this.ctxDrawing.drawImage(item, 0, 0);
                activeLayer.classList.remove('layer--active');
                item.classList.add('layer--active');
            }
        })
        activeItem.classList.remove('item--active');
        e.target.classList.add('item--active');
    }
    onChangeActiveLayer = () => {
        const layerListWrap = document.querySelector('.layersList__wrapper');
        layerListWrap.addEventListener('click', this.changeActiveLayer, false);
    }
    offChangeActiveLayer = () => {
        const layerListWrap = document.querySelector('.layersList__wrapper');
        layerListWrap.removeEventListener('click', this.changeActiveLayer, false);
    }
    moveLayer = (e) => {
        const activeLayer = document.querySelector('.layer--active');
        const id = activeLayer.dataset.id;
        const layers = this.state.layers;
        let indexSwapItem = 0;
        layers.forEach( (elem, i) => {
            if (elem.id === +id) {
                indexSwapItem = i;
            }
        });
        if (e.target.classList.contains('button__layers--up')) {
            layers.splice(indexSwapItem + 1, 0, layers.splice(indexSwapItem, 1)[0]);
        } else if (indexSwapItem !== 0) {
            layers.splice(indexSwapItem - 1, 0, layers.splice(indexSwapItem, 1)[0]);
        }        
        this.setState ({
            layers,
        });
    }

    onMoveLayer = () => {
        const moveUp = document.querySelector('.button__layers--up');
        const moveDown = document.querySelector ('.button__layers--down');
        moveUp.addEventListener('click', this.moveLayer, false);        
        moveDown.addEventListener('click', this.moveLayer, false);    
    }
    addNewLayer = () => {
        this.redrawToLayer();
        this.deleteActiveClass();
        const layers = this.state.layers;
        const nextID = this.state.currentID + 1;
        layers.push({
            width: "640",
            height: "640",
            scale: "0.8",
            name: "Layer1",
            opacity: "0.4",
            zIndex: this.state.currentIndex,
            id: this.state.currentID
        })
        this.setState({
            layers,
            currentID: nextID,
        })
    }
    onAddNewLayer = () => {
        const addBut = document.querySelector('.button__layers--plus');
        addBut.addEventListener('click', this.addNewLayer, false);
    }
    offAddNewLayer = () => {
        const addBut = document.querySelector('.button__layers--plus');
        addBut.removeEventListener('click', this.addNewLayer, false);
    }
    deleteActiveClass = () => {
        const activeItem = document.querySelector('.layer--active');
        activeItem.classList.remove('layer--active');
    }
    redrawToLayer = () => {
        const currentLayer = document.querySelector('.layer--active');        
        const currentCtx = currentLayer.getContext('2d');
        currentCtx.drawImage(this.canvasDrawing, 0, 0);
        this.ctxDrawing.clearRect(0, 0, this.canvasDrawing.width, this.canvasDrawing.height);
    }
    deleteLayer = () => {
        const activeLayer = document.querySelector('.layer--active');
        const id = activeLayer.dataset.id;
        const layers = this.state.layers;
        let idPrev = 0;
        layers.forEach( (elem, i) => {
            if (elem.id === +id) {
                if ( layers[i - 1] ) {
                    idPrev = layers[i - 1].id
                } else if ( layers[i + 1] ) {
                    idPrev = layers[i + 1].id    
                } else return;                
                layers.splice(i, 1);
            }
        });
        const drawLayers = document.querySelectorAll('.canvas--layer');
        const arrDrawLayers = Array.from(drawLayers);
        arrDrawLayers.forEach( (elem) => {
            if (+elem.dataset.id === idPrev) {
                elem.classList.add('layer--active');
            }
        })
        this.setState ({
            layers,
        })
        this.ctxDrawing.clearRect(0, 0, this.canvasDrawing.width, this.canvasDrawing.height);
    }
    onDeleteLayer = () => {
        const deleteBut = document.querySelector('.button__layers--delete');
        deleteBut.addEventListener('click', this.deleteLayer, false);
    }
    offDeleteLayer = () => {
        const deleteBut = document.querySelector('.button__layers--delete');
        deleteBut.removeEventListener('click', this.deleteLayer, false);
    }
    componentDidMount() {
        this.onMount();
        this.onAddNewLayer();
        this.onChangeActiveLayer();
        this.onDeleteLayer();
        this.onMoveLayer();
    }
    componentWillUnmount() {
        this.offDeleteLayer();
        this.offAddNewLayer();
        this.offChangeActiveLayer();
    }
    renderLayers(arr) {
        return arr.map(item => {
            const {width, height, scale, name, id, opacity} = item;
            return (
                    <Layer 
                    width={width}
                    height={height}
                    scale={scale}
                    name={name}
                    opacity={opacity}
                    key={id}
                    id={id}
                    />
            )
        })
    }
    render () {
        const {error, layers} = this.state;        
        if (error) {
           // return <ErrorMessage/>
        }
        const items = this.renderLayers(layers);

        return (
            <div className="layers__container"> 
                <canvas width="640" height="640" className=" canvas__drawing canvas" data-scale="0.8"></canvas>             
                {items}               
                <canvas width="640" height="640" className="canvas__hidden"></canvas>
            </div>
        );
    }

}
