import React, {Component} from 'react';
import './CursosCoordinates.css';

export default class CursosCoordinates extends Component {
    state = {
        coordX: 0, 
        coordY: 0,
    }
    currentScale = ()  => {
        const canvasDrawing = document.querySelector('.canvas__drawing');
        const scale = canvasDrawing.dataset.scale;
        const coordinatesZoom = document.querySelector('.coordinates__zoom');
        coordinatesZoom.innerHTML = `x${Math.round( (+scale + 0.2)  * 100) / 100}`;
    }
    currentFramesCount = () => {
        const frames = document.querySelectorAll('.frame__item');
        const coordinatesFrame = document.querySelector('.coordinates__frame');
        const framesArr = Array.from(frames);
        const activeFrame = document.querySelector('.frame--active').parentNode.parentNode.parentNode;
        const activeFrameKey = activeFrame.dataset.key;
        let activeFrameIndex;
        framesArr.forEach( (item, i) => {
            if (item.dataset.key === activeFrameKey) {
                activeFrameIndex = i;
            }
        })
        coordinatesFrame.innerHTML = `${activeFrameIndex +1}/${framesArr.length}`;
    }
    onCoordinatesFrames= () => {
        const framesWrap = document.querySelector('.frames__container');
        framesWrap.addEventListener('click', this.currentFramesCount, false);
    }
    onCurrentScale = () => {
        const canvasDrawing = document.querySelector('.canvas__drawing');
        canvasDrawing.addEventListener('wheel', this.currentScale, {passive: true});
    }
    umountCoordinates () {
        const framesWrap = document.querySelector('.frame__list');
        framesWrap.removeEventListener('click', this.currentFramesCount, false);
        const canvasDrawing = document.querySelector('.canvas__drawing');
        canvasDrawing.removeEventListener('wheel', this.currentScale, false);
    }
    cursorPosition = (e) => {
        const canvasDrawing = document.querySelector('.canvas__drawing');
        const inputSize = document.querySelector('.setting__label--width');
        const height = canvasDrawing.height;
        const size = inputSize.value;
        const widthPixel = height/size;
        const coordX = Math.floor(e.offsetX/widthPixel) + 1;
        const coordY = Math.floor(e.offsetY/widthPixel) + 1;
        this.setState ({
            coordX,
            coordY,
        })
    }
    onCursorPosition = () => {
        const canvasDrawing = document.querySelector('.canvas__drawing');
        canvasDrawing.addEventListener('mousemove', this.cursorPosition, false);
    }    
    componentDidMount() {
        this.onCursorPosition();
        this.onCoordinatesFrames();
        this.onCurrentScale();
    }
    componentWillUnmount() {
        this.umountCoordinates();
    }
    render() {
        return (
            <div className="coordinates__container">
                <div className="coordinates__frame">1/1</div>
                <div className="coordinates__zoom">x1.00</div>  
                <div className="coordinates__cursor">{this.state.coordX} : {this.state.coordY}</div>    
                <div className="coordinates__size">[32: 32]</div>        
            </div>
        );
    }  
}
