import React, {Component} from 'react';

export default class Layer extends Component {
    render () {
        return (
            <canvas width={this.props.width} height={this.props.height} 
            className="canvas canvas--layer layer--active" data-scale={this.props.scale}
            data-name={this.props.name} data-id = {this.props.id} style={{ opacity: this.props.opacity}}></canvas>
        );
    }    
}
