import React, {Component} from 'react';
import './LayersPanel.css';
import LayerPanelItem from './LayerPanelItem';

export default class LayersPanel extends Component {

    state = {
        layers: [
            {
                name: "Layer 1",
                id: 1
            }
        ],
        currentID: 2, 
        error: false, 
        currentIndex: 0,
    }
    deleteLayer = () => {
        const activeLayer = document.querySelector('.item--active');
        const id = activeLayer.dataset.id;
        const layers = this.state.layers;
        let idPrev = 0;
        layers.forEach( (elem, i) => {
            if (elem.id === +id) {
                if ( layers[i - 1] ) {
                    idPrev = layers[i - 1].id
                } else if ( layers[i + 1] ) {
                    idPrev = layers[i + 1].id    
                } else return;               
                layers.splice(i, 1);
            }
        });
        const drawLayers = document.querySelectorAll('.layersList__item');
        const arrDrawLayers = Array.from(drawLayers);
        arrDrawLayers.forEach( (elem) => {
            if (+elem.dataset.id === idPrev) {
                elem.classList.add('item--active');
            }
        })
        this.setState ({
            layers,
        })
    }
    moveLayerUp = (e) => {
        const activeItem = document.querySelector('.item--active');
        const id = activeItem.dataset.id;
        const layers = this.state.layers;
        let indexSwapItem = 0;
        layers.forEach( (elem, i) => {
            if (elem.id === +id && i !== 0) { 
               indexSwapItem = i;
            }
        });
        if (e.target.classList.contains('button__layers--up') && indexSwapItem!== 0) {
            console.log(indexSwapItem);
            layers.splice(indexSwapItem - 1, 0, layers.splice(indexSwapItem, 1)[0]);
        } else if ( e.target.classList.contains('button__layers--down') ) {
            layers.splice(indexSwapItem + 1, 0, layers.splice(indexSwapItem, 1)[0]);
        }
        
        this.setState ({
            layers,
        });
    }
    onMoveLayer = () => {
        const moveUp = document.querySelector('.button__layers--up');
        const moveDown = document.querySelector ('.button__layers--down');
        moveUp.addEventListener('click', this.moveLayerUp, false);        
        moveDown.addEventListener('click', this.moveLayerUp, false);   
    }
    onDeleteLayer = () => {
        const deleteBut = document.querySelector('.button__layers--delete');
        deleteBut.addEventListener('click', this.deleteLayer, false);
    }
    addNewLayer = () => {
        this.deleteActiveClass();
        const layers = this.state.layers;
        const nextID = this.state.currentID + 1;
        const name = `Layer ${this.state.currentID}`;
        layers.unshift({
            name: name,
            id: this.state.currentID
        })
        this.setState({
            layers,
            currentID: nextID,
        })        
    }
    deleteActiveClass = () => {
        const activeItem = document.querySelector('.item--active');
        activeItem.classList.remove('item--active');
    }
    onAddNewLayer = () => {
        const addBut = document.querySelector('.button__layers--plus');
        addBut.addEventListener('click', this.addNewLayer, false);
    }
    renderItem = (arr) => {
        return arr.map(item => {
            const {name, id} = item;
            return (
                <LayerPanelItem 
                    name={name}
                    key={id}
                    id={id}
                />
            )
        })
    }
    componentDidMount() {
        this.onAddNewLayer();
        this.onDeleteLayer();
        this.onMoveLayer();
    }
    render() {
        const {error, layers} = this.state;        
        if (error) {
           // return <ErrorMessage/>
        }
        const items = this.renderItem(layers);

        return (
            <div className="layersPanel__container">
                <h3 className="layersPanel__title">Layers</h3>
                <div className="buttons__container--layers">
                   <button className="button__layers button__layers--plus"></button>
                   <button className="button__layers button__layers--up"></button>
                   <button className="button__layers button__layers--down"></button>
                   <button className="button__layers button__layers--delete"></button>
                </div>
                <ul className="layersList__wrapper">
                    {items}
                </ul>
            </div>
        );
    }    
}
