import getSize from '../../SettingsMenu/Resize/getSize';
import redDraw from '../ToolsFunction/Redraw';


export default function PaintAll(e) {
    const canvasDrawing = document.querySelector('.canvas__drawing');
    const ctx = canvasDrawing.getContext('2d');
    const canvasSize= getSize();
    const widthPixel = canvasDrawing.width / canvasSize;
    let tool;
    let color;
    const activeFrame = document.querySelector('.frame--active');
    ctx.drawImage(activeFrame, 0, 0);

    const draw = (e) => {
        const x0 = e.offsetX;
        const y0 = e.offsetY;
        const startX = Math.floor(x0 / widthPixel) * widthPixel;
        const startY = Math.floor(y0 / widthPixel) * widthPixel;
        const imgData = ctx.getImageData(startX, startY, 1, 1);
        const red = imgData.data[0];
        const green = imgData.data[1];
        const blue = imgData.data[2];
        tool = document.querySelector('.tool_paint--all');


           
        if (e.which === 1) {
            color = document.querySelector('.color__primary').value;
        }
        if (e.which === 3) {
            color = document.querySelector('.color__secondary').value;
        }
        if (tool.classList.contains('tool--active')) {
            for (let x = 0; x < canvasDrawing.width; x += widthPixel) {
                for (let y = 0; y < canvasDrawing.height; y += widthPixel) {
                    const imgDataPx = ctx.getImageData(x, y, 1, 1);
                    if (imgDataPx.data[0] === red && imgDataPx.data[1] === green
                    && imgDataPx.data[2] === blue) {
                    ctx.fillStyle = color;
                    ctx.fillRect(x, y, widthPixel, widthPixel);
                    }
                };  
             };
        }
        else {
            canvasDrawing.removeEventListener('click', draw, false);
        }
        redDraw();
    };

    canvasDrawing.addEventListener('click', draw, false);
};
