import getColor from '../../Palette/getColor';
import Redraw from './Redraw';

export default function paintBicket(e) {
    const canvas = document.querySelector('.canvas__drawing');
    const ctx = canvas.getContext("2d");

    const getPixelPos = function (x, y) {
        return (y * canvas.width + x) * 4;
    };

    const matchStartColor = function (data, pos, startColor) {
    return (data[pos]   === startColor.r &&
            data[pos+1] === startColor.g &&
            data[pos+2] === startColor.b &&
            data[pos+3] === startColor.a);
    };

    const colorPixel = function (data, pos, color) {
        data[pos] = color.r || 0;
        data[pos+1] = color.g || 0;
        data[pos+2] = color.b || 0;
        data[pos+3] = color.hasOwnProperty("a") ? color.a : 255;
    };

    const floodFill = function (startX, startY, fillColor) {
        const activeTool = document.querySelector('.tool--active');
        if (activeTool.classList.contains('tool__paint--bucket')) { 
            const dstImg = ctx.getImageData(0,0,canvas.width,canvas.height);
            let dstData = dstImg.data;
            
            const startPos = getPixelPos(startX, startY);
            let startColor = {
                r: dstData[startPos],
                g: dstData[startPos+1],
                b: dstData[startPos+2],
                a: dstData[startPos+3]
            };
            if (startColor.r === fillColor.r && startColor.g === fillColor.g && 
                startColor.b === fillColor.b && startColor.a === fillColor.a) {
                return;
            }
            const todo = [[startX,startY]];
            
            while (todo.length) {
                let pos = todo.pop();
                let x = pos[0];
                let y = pos[1];    
                let currentPos = getPixelPos(x, y);
                
                while((y-- >= 0) && matchStartColor(dstData, currentPos, startColor)) {
                currentPos -= canvas.width * 4;
                }
                
                currentPos += canvas.width * 4;
                ++y;
                let reachLeft = false;
                let reachRight = false;
                
                while((y++ < canvas.height-1) && matchStartColor(dstData, currentPos, startColor)) {
                
                colorPixel(dstData, currentPos, fillColor);
                
                if (x > 0) {
                    if (matchStartColor(dstData, currentPos-4, startColor)) {
                    if (!reachLeft) {
                        todo.push([x-1, y]);
                        reachLeft = true;
                    }
                    }
                    else if (reachLeft) {
                    reachLeft = false;
                    }
                }
                
                if (x < canvas.width-1) {
                    if (matchStartColor(dstData, currentPos+4, startColor)) {
                    if (!reachRight) {
                        todo.push([x+1, y]);
                        reachRight = true;
                    }
                    }
                    else if (reachRight) {
                    reachRight = false;
                    }
                }

                currentPos += canvas.width * 4;
                }
            }
            ctx.putImageData(dstImg,0,0);
            Redraw();          
        } else {
            canvas.removeEventListener('click', addEvent, false);
        }
    };
    
        const addEvent = (e) => {
            const color = getColor();
            const startX = e.offsetX;
            const startY = e.offsetY;          
            floodFill(startX, startY, {r:color[0], g:color[1], b:color[2], a:255});
        };
        canvas.addEventListener('click', addEvent, false);             
}
