import getSize from '../../SettingsMenu/Resize/getSize';

export default function Rectangle() {
    const canvasDrawing = document.querySelector('.canvas__drawing');
    const ctx = canvasDrawing.getContext('2d');
    let color;


    const draw = (e) => {
        const canvasSize= getSize();
        const widthPixel = canvasDrawing.width / canvasSize;
        const activeFrame = document.querySelector('.frame--active');
        const ctxFrame = activeFrame.getContext('2d');
        const tool = document.querySelector('.tool__rectangle'); 
        const redraw = () => {
            canvasDrawing.onmousemove = null;             
            ctxFrame.drawImage(canvasDrawing, 0, 0);
            ctx.clearRect(0, 0, canvasDrawing.width, canvasDrawing.width);
            ctx.drawImage(activeFrame, 0, 0);
        }
        if (tool.classList.contains('tool--active')) {
            if (e.which === 1) {
                color = document.querySelector('.color__primary').value;
            }
            if (e.which === 3) {
                color = document.querySelector('.color__secondary').value;
            }    
            ctx.fillStyle = color;    
            const x1 = e.offsetX;
            const y1 = e.offsetY;
            const coordX1 = Math.floor(x1 / widthPixel);
            const coordY1 = Math.floor(y1 / widthPixel);
            const startX = coordX1 * widthPixel;
            const startY = coordY1 * widthPixel;
            canvasDrawing.onmousemove = (event) => {
                const canvas3 = document.querySelector('.frame--active');
                const x2 = event.offsetX;
                const y2 = event.offsetY;
                const coordX2 = Math.floor(x2 / widthPixel);
                const coordY2 = Math.floor(y2 / widthPixel);
                const endX = coordX2 * widthPixel;
                const endY = coordY2 * widthPixel;
                ctx.clearRect(0, 0, canvasDrawing.width, canvasDrawing.width);
                canvasDrawing.getContext('2d').drawImage(canvas3, 0, 0);
                ctx.fillRect(startX, startY, endX - startX, widthPixel);
                ctx.fillRect(startX, startY, widthPixel, endY - startY);
                ctx.fillRect(endX + widthPixel, endY, startX - endX - widthPixel, widthPixel);
                ctx.fillRect(endX, endY, widthPixel, startY - endY);
                canvasDrawing.addEventListener('mouseup', redraw, false);
            };
        } else {
            canvasDrawing.removeEventListener('mousedown', draw, false);
            canvasDrawing.removeEventListener('mouseup', redraw, false);
        }
    }
    canvasDrawing.addEventListener('mousedown', draw, false);
}
