import React, {Component} from 'react';
import './Resize.css';

export default class SettingMenu extends Component {
    state = {
        size: 32,
    }
    changeSize = (e) => {  
        const currentSize = e.target.value; 
        this.setState({
            previousSize: this.state.size,
            size: currentSize,
        });
        this.resize();
    }
    resize = () => {
        const canvas = document.querySelectorAll('.canvas');
        const canvasArr = Array.from(canvas);
        const canvasHidden = document.querySelector('.canvas__hidden');
        const ctxHidden = canvasHidden.getContext('2d');
        const ratio = this.state.size/this.state.previousSize;
        const coordinatesSize = document.querySelector('.coordinates__size');

        canvasArr.forEach( (elem) => {
            const ctx = elem.getContext('2d');
            ctxHidden.clearRect(0, 0, elem.width, elem.height);
            ctxHidden.imageSmoothingEnabled = false;
            ctx.imageSmoothingEnabled = false;
            ctxHidden.drawImage(elem, 0, 0, elem.width, elem.height);
            ctx.clearRect(0, 0, elem.width, elem.height);
            ctx.drawImage(canvasHidden, 0, 0, elem.width/ratio, elem.width/ratio);
        });
        coordinatesSize.innerHTML=`[${this.state.size}: ${this.state.size}]`
    }
    changeInnerInput = (e) => {
        const inputWidth = document.querySelector('.setting__label--width');
        const inputHeight = document.querySelector('.setting__label--height');
        const target = e.target;
        if (target.classList.contains('setting__label--width')) {
            inputHeight.value = target.value;
        }
        else {
            inputWidth.value = target.value;
        }
    }
    onChangeSize = () => {
        const inputWidth = document.querySelector('.setting__label--width');
        const inputHeight = document.querySelector('.setting__label--height');
        inputWidth.addEventListener('change', this.changeSize, false);
        inputHeight.addEventListener('change', this.changeSize, false);        
        inputWidth.addEventListener('input', this.changeInnerInput, false);
        inputHeight.addEventListener('input', this.changeInnerInput, false);
    }
    removeChangeSize = () => {
        const inputWidth = document.querySelector('.setting__label--width');
        const inputHeight = document.querySelector('.setting__label--height');
        inputWidth.removeEventListener('change', this.changeSize, false);
        inputHeight.removeEventListener('change', this.changeSize, false);
        inputWidth.removeEventListener('input', this.changeInnerInput, false);
        inputHeight.removeEventListener('input', this.changeInnerInput, false);
    }
    componentDidMount() {
        this.onChangeSize();
    }
    componentWillUnmount() {
        this.removeChangeSize();
    }
    render() {
        return (
            <div className="resize">
                <span className="radio__description">Canvas size</span>
                <div className="input__wrapper">
                    <div className="resize__section">
                        <label className="setting__label">Width:</label>
                        <input type="text" id="name" name="name" required  maxLength="3" size="10" 
                        className="input__text setting__label--width" defaultValue={this.state.size} data-size={this.state.size}/>
                    </div>
                    <div className="resize__section">
                        <label className="setting__label">Height:</label>
                        <input type="text" id="name" name="name" required  maxLength="3" size="10" 
                        className="input__text setting__label--height" defaultValue={this.state.size} data-size={this.state.size}/>
                    </div>
                    <button className="button button__header">Resize</button>
                </div>
            </div> 
        );
    }
}
