import React, {Component} from 'react';
import './ShortCatItem.css';

export default class ShortcatItem extends Component {
    render () {
        return (
            <>
                <div className="shortcat__wrapper">
                    <img src="" alt=""></img>
                    <span className="shortcat__name">{this.props.name}</span>
                    <span className="shortcat__description">{this.props.shortcat}</span>
                    <input className="shortcat__input" type="text" minLength="1" maxLength="1"
                     data-key={this.props.shortcat}  defaultValue={this.props.shortcat} required></input>
                </div>
            </>
        );
    }    
}
