import React, {Component} from 'react';
import 'core-js';
import { Draggable, Droppable } from 'react-drag-and-drop'
import './frameList.css';
import ls from 'local-storage';

export default  class FrameList extends Component {

    startDropIndex = 0;
    endDropIndex = 0;

    state = {
        frames: [],
        id: [],
        countID: 1,
        currentFramesCounter: 1,
        currentFramesId: [1],
    }  
    saveSession = () => {
        ls.set('FrameList', JSON.stringify(this.state));
    }
    loadSession = () => {
        const framesInfo = JSON.parse(ls.get('FrameList'));
        this.setState ({
            // frames: framesInfo.frames,
            id: framesInfo.id,
            countID: framesInfo.countID,
            currentFramesCounter: framesInfo.currentFramesCounter,
            currentFramesId: framesInfo.currentFramesId,
        })
    }
    test = () => {
        // const button = document.querySelector('.button__header');
        // button.addEventListener('click', this.loadSession, false);
    }
    addNewFrame() {
        const arrFramesId = this.state.currentFramesId;
        if (this.state.currentFramesCounter !== 1) {
            arrFramesId.push(this.state.currentFramesCounter);
        };
        const element = this.NewFrame(this.state.countID, this.state.currentFramesId[this.state.currentFramesCounter - 1]);
        const arrFrames = this.state.frames;
        arrFrames.push(element);
        this.setState ( {
            frames: arrFrames,
            countID: this.state.countID + 1,
            currentFramesCounter: this.state.currentFramesCounter + 1,
            currentFramesId: arrFramesId,
        });
    }
    onAddNewFrame = () => {
        const addFrame = () => {
            document.querySelector('.frame--active').classList.remove('frame--active');
            this.addNewFrame();
            const canvasDrawing = document.querySelector('.canvas__drawing');
            const ctx = canvasDrawing.getContext('2d');
            ctx.clearRect(0, 0, canvasDrawing.width, canvasDrawing.height);
            this.saveSession();
        } 
        const addButton = document.querySelector('.frames__button');
        addButton.addEventListener('click', addFrame, false);
    }
    onFramesTools() {
        const changeFrame = (e) =>  {
            const target = e.target;
            const canvasDrawing = document.querySelector('.canvas__drawing');
            const ctx = canvasDrawing.getContext('2d');
            if (target.classList.contains('canvas')) {
                const activeFrame = document.querySelector('.frame--active');
                activeFrame.classList.remove('frame--active');
                target.classList.add('frame--active');
                ctx.clearRect(0, 0, canvasDrawing.width, canvasDrawing.height);
                ctx.drawImage(target, 0, 0);
            }
        }
        const deleteFrame = (e) => {
            const target = e.target;
            if (target.classList.contains('tolltip__button--delete')) { 
                if (this.state.frames.length > 1) { 
                    const frames = this.state.frames;
                        console.log(this.state.frames);
                        const key = e.target.parentNode.querySelector('.tolltip__button--number').innerHTML;
                        frames.splice(key-1, 1);
                        const currentIdArr = this.state.currentFramesId;
                        const frameCounter = this.state.currentFramesCounter - 1;
                        currentIdArr.pop();
                        this.setState ({
                            frames: frames,
                            currentFramesCounter: frameCounter,
                            currentFramesId: currentIdArr
                        });
                        const queryframes = document.querySelectorAll('.frame');
                        const arrFrames = Array.from(queryframes);
                        if (target.parentNode.querySelector('.frame--active')) {
                            arrFrames[arrFrames.length-1].classList.add('frame--active');
                        }
                        const queryButNumbers = document.querySelectorAll('.tolltip__button--number');
                        const arrButNumbers = Array.from(queryButNumbers);
                        arrButNumbers.forEach( (elem, i) => {
                            elem.innerHTML = i + 1;
                        });
                    }
                    else {
                        const firstFrame = e.target.parentNode.querySelector('.canvas');
                        const ctxFrame = firstFrame.getContext('2d');
                        const canvasDrawing = document.querySelector('.canvas__drawing');
                        const ctxDrawing = canvasDrawing.getContext('2d');
                        ctxFrame.clearRect(0, 0, firstFrame.width, firstFrame.height);
                        ctxDrawing.clearRect(0, 0, canvasDrawing.width, canvasDrawing.height);                        
                    }
            }             
        }
        const dublicateFrame = (e) => {
            const target = e.target;
            let elemKey = 0;
            let targetCanvas;
            if (e.target.classList.contains('tolltip__button--dublicate')) { 
                const arrFramesId = this.state.currentFramesId;
                const targetKey = target.parentNode.dataset.key;
                const arrFrames = this.state.frames;
                if (this.state.currentFramesCounter !== 1) {
                    arrFramesId.push(this.state.currentFramesCounter);
                };
                arrFrames.forEach( (elem, i) => {
                    if (elem.key === targetKey) {
                        const element = this.NewFrame(this.state.countID, this.state.currentFramesId[this.state.currentFramesCounter - 1]);
                        arrFrames.splice(i+1,0, element);                       
                        elemKey = element.key;
                        const targetFrame = document.querySelector(`[data-key='${elem.key}']`);
                        targetCanvas =   targetFrame.querySelector('.canvas');
                    }
                });
                this.setState ( {
                    frames: arrFrames,
                    countID: this.state.countID + 1,
                    currentFramesCounter: this.state.currentFramesCounter + 1,
                    currentFramesId: arrFramesId,
                });
                const queryButNumbers = document.querySelectorAll('.tolltip__button--number');
                const arrButNumbers = Array.from(queryButNumbers);
                arrButNumbers.forEach( (elem, i) => {
                    elem.innerHTML = i + 1;
                });
                const newFrame = document.querySelector(`[data-key='${elemKey}']`);
                const newFrameCanvas = newFrame.querySelector('.canvas');
                newFrameCanvas.classList.remove('frame--active');
                const ctx = newFrameCanvas.getContext('2d');
                ctx.drawImage(targetCanvas, 0, 0);
            }
        }
        const framesContainer = document.querySelector('.frames__container');
        framesContainer.addEventListener('click', changeFrame, false);
        framesContainer.addEventListener('click', deleteFrame, false);
        framesContainer.addEventListener('click', dublicateFrame, false);
    } 
    NewFrame(id,number) {
        return (
           <li className="frame__item"  key={id} data-key={id} data-order={number}>
                <Draggable type="frame" data={id} key={id}>
                    <div className="frame__wrapper ">
                        <canvas width="640" height="640" className="frame canvas frame--active"></canvas>
                    </div>
                    <button className="tolltip__button tolltip__button--number tooltip__button--active">{number}</button>
                    <button className="tolltip__button tolltip__button--delete"></button>
                    <button className="tolltip__button tolltip__button--dublicate"></button>
                    <button className="tolltip__button tolltip__button--drag"></button>    
                </Draggable>
            </li>
        )
    }
    onDrop(data,types) {
        const target = types.target.parentNode.parentNode.parentNode;
        let currentSwapItem = 0;
        let withSwapItem = 0;
        let framesArr = this.state.frames;
        this.state.frames.forEach( (elem, i) => {
            if (elem.key === target.dataset.key) {
                this.endDropIndex = i;
            }
        })
        currentSwapItem = framesArr[this.startDropIndex];
        withSwapItem = framesArr[this.endDropIndex];
        framesArr[this.endDropIndex] = currentSwapItem;
        framesArr[this.startDropIndex] = withSwapItem;
        this.setState ({
            frames: framesArr,
        })
    }    
    onDragStart(e) {
        const target = e.target.parentNode;
        this.state.frames.forEach( (elem, i) => {
            if (elem.key === target.dataset.key) {
                this.startDropIndex = i;
            }
        })
    }
    componentDidMount() {
        this.addNewFrame();
        this.onAddNewFrame();
        this.onFramesTools();
        // this.loadSession();
        this.test();
    }
    componentWillUnmount() {
        
    }
    render() {
        return (
            <div className="frames__container">
                <Droppable
                types={['frame']}
                onDrop={this.onDrop.bind(this)}
                onDragStart={this.onDragStart.bind(this)}>            
                <ul className="frame__list">
                    {this.state.frames}
                </ul>
                </Droppable>
                <div className="frames__button">
                    <div className="icon__plus"></div>
                    <div className="frames__label">Add new frame</div>
                </div>
            </div>
        );
    }
}
