export default function ColorPicker() {
    const canvasDrawing = document.querySelector('.canvas__drawing');
    const ctx = canvasDrawing.getContext("2d");
    const primaryColor = document.querySelector('.color__primary');
    const secondaryColor = document.querySelector('.color__secondary');
    const colorPrimWrap = document.querySelector('.color__wrapper--primary');
    const colorPrimSecond = document.querySelector('.color__wrapper--secondary');

    const getColor = (e) => {
        const tool = document.querySelector('.tool__color--picker'); 
        if (tool.classList.contains('tool--active')) {
            const startX = e.offsetX;
            const startY = e.offsetY;  
            const getPixelPos = function (x, y) {
                return (y * canvasDrawing.width + x) * 4;
            };        
            const startPos = getPixelPos(startX, startY);
            const dstImg = ctx.getImageData(0, 0, canvasDrawing.width, canvasDrawing.height);
            const dstData = dstImg.data;
            const startColor = {
                r: dstData[startPos],
                g: dstData[startPos+1],
                b: dstData[startPos+2],
            };
            function rgbToHex(r, g, b) {
                return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
            }
            const color = rgbToHex(startColor.r, startColor.g, startColor.b);
            if (e.which === 1) {
                primaryColor.value = color;
                colorPrimWrap.style.background = color;
            } else if (e.which === 3) {
                secondaryColor.value = color;
                colorPrimSecond.style.background = color;
            }
        } else {
            canvasDrawing.removeEventListener('click', getColor, false);
        }
    }
    canvasDrawing.addEventListener('click', getColor, false);
}
    

    
