export default function Rotate() {
    const canvasDrawing = document.querySelector('.canvas__drawing');
    const ctx = canvasDrawing.getContext('2d');
    const activeFrame = document.querySelector('.frame--active');
    const ctxActive = activeFrame.getContext('2d');
    ctx.save();
    ctx.clearRect(0, 0, canvasDrawing.width, canvasDrawing.width);
    ctx.translate(canvasDrawing.width / 2, canvasDrawing.width / 2);
    ctx.rotate((Math.PI / 180) * 90);
    ctx.drawImage(activeFrame, -canvasDrawing.width / 2, -canvasDrawing.width / 2);
    ctxActive.clearRect(0, 0, activeFrame.width, activeFrame.width);
    ctxActive.drawImage(canvasDrawing, 0, 0);
    ctx.restore();
  }
